<template>
  <el-container class="container">
    <el-header>
      <h1>农历生日生肖及评分</h1>
    </el-header>
    <el-main>
      <el-form :model="form" label-width="120px" class="cente-form">
        <el-form-item label="农历年份">
          <el-input v-model="form.year" placeholder="请输入农历年份"></el-input>
        </el-form-item>
        <el-form-item label="农历月份">
          <el-input v-model="form.month" placeholder="请输入农历月份"></el-input>
        </el-form-item>
        <el-form-item label="农历日期">
          <el-input v-model="form.day" placeholder="请输入农历日期"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSubmit">提交</el-button>
        </el-form-item>
      </el-form>

      <el-card v-if="result.zodiac" class="result-card">
        <h2>生肖: {{ result.zodiac }}</h2>
        <h2>生日评分: {{ result.score }} 分</h2>
        <h2>生日档次: {{ result.grade }}</h2>
      </el-card>
    </el-main>
  </el-container>
</template>

<script>
import { ref } from 'vue';

export default {
  setup() {
    const form = ref({
      year: '',
      month: '',
      day: '',
    });

    const result = ref({
      zodiac: null,
      score: null,
      grade: null,
    });

    const getZodiac = (year) => {
      const zodiacs = ["鼠", "牛", "虎", "兔", "龙", "蛇", "马", "羊", "猴", "鸡", "狗", "猪"];
      return zodiacs[(year - 4) % 12];
    };

    const scoreBirthday = (year, month, day) => {
      let score = 0;

      // 月份评分
      if (month >= 1 && month <= 3) {
        score += 20;
      } else if (month >= 4 && month <= 6) {
        score += 15;
      } else if (month >= 7 && month <= 9) {
        score += 25;
      } else if (month >= 10 && month <= 12) {
        score += 30;
      }

      // 日期评分
      if (day % 2 === 0) {
        score += 20;
      } else {
        score += 10;
      }

      // 生肖加成
      const zodiac = getZodiac(year);
      if (zodiac === "龙" || zodiac === "虎") {
        score += 10;
      } else {
        score += 5;
      }

      return score;
    };

    const getBirthdayGrade = (score) => {
      if (score >= 80) {
        return "华诞";
      } else if (score >= 60) {
        return "良辰";
      } else if (score >= 40) {
        return "吉日";
      } else if (score >= 20) {
        return "平常";
      } else {
        return "一般";
      }
    };

    const handleSubmit = () => {
      const { year, month, day } = form.value;
      const zodiac = getZodiac(year);
      const score = scoreBirthday(year, month, day);
      const grade = getBirthdayGrade(score);

      result.value.zodiac = zodiac;
      result.value.score = score;
      result.value.grade = grade;
    };

    return {
      form,
      result,
      handleSubmit,
    };
  },
};
</script>

